@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto+Mono:ital,wght@0,100..700;1,100..700&display=swap");

:root {
  --theme-color: oklch(0.558 0.288 302.321);
  --text-color: #ffffff;
  --grey: oklch(0.869 0.022 252.894);
  --light-grey: oklch(0.968 0.007 247.896);
}

body {
  margin: 0;
  font-family: Poppins, Arial, sans-serif;
  background-image: url("https://source.unsplash.com/1600x900/?blockchain,security");
  background-size: cover;
  background-position: center;
  color: white;
  text-align: center;
}

.navbar {
  background: var(--theme-color);
  padding: 15px 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: background-color 0.5s ease-in-out, transform 0.3s ease-in-out;
}

.navbar:hover {
  background: oklch(0.658 0.288 302.321);
  transform: translateY(-5px);
}

.navbar h1 {
  text-align: center;
  margin: 0;
  padding: 1.2rem;
  font-size: 24px;
}

.navbar ul {
  list-style: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
  gap: 15px;
}

.navbar ul li a {
  color: var(--text-color);
  text-decoration: none;
  cursor: pointer;
  font-size: 18px;
  transition: color 0.5s, transform 0.3s;
}

.navbar ul li a:hover {
  color: #ffcc00;
  transform: scale(1.1);
}

.end-line {
  background-color: var(--grey);
  padding: 0.1rem;
}

.founder-image-container {
  padding: 1.5rem 0rem 1.5rem 0rem;
  background-color: var(--grey);
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem 0 1rem 0rem;
}

.founder-image {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
  border: 5px solid white;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
}

.introduction-section {
  background: var(--light-grey);
  color: var(--theme-color);
  padding: 20px;
  margin: 30px auto;
  max-width: 600px;
  border-radius: 10px;
}
